.more-button {
  width: 34px;
  height: 34px;
  cursor: pointer;
  color: #fff;
  z-index: 2;
  background-color: #ff001b;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  transition: all .2s ease-in;
  display: flex;
  position: relative;
  box-shadow: 0 0 0 4px rgba(255, 0, 27, .42);
}

.more-button:hover, .more-button:focus {
  background-color: #eb0019;
  animation: shadow-animation .3s forwards;
}

.more-button:focus {
  outline: 0;
}

.more-button_img {
  cursor: pointer;
}

.more-button_img > div {
  width: 18px;
  height: 2px;
  background: azure;
  border-radius: 2px;
}

.more-button_img > div:before, .more-button_img > div:after {
  content: "";
  width: 18px;
  height: 2px;
  background: azure;
  border-radius: 2px;
  display: block;
  position: absolute;
}

.more-button_img > div:before {
  margin-top: -6px;
}

.more-button_img > div:after {
  margin-top: 6px;
}

.more-button_img-anim {
  transition: all .3s cubic-bezier(.68, -.35, .265, 1.35);
}

.more-button_img-anim > div {
  background: none;
  transition: all .3s;
  transform: rotate(180deg);
}

.more-button_img-anim > div:before {
  transform: rotate(45deg)translate(4px, 4px);
}

.more-button_img-anim > div:after {
  transform: rotate(-45deg)translate(4px, -4px);
}

.more-button-list {
  width: 220px;
  height: 178px;
  opacity: 0;
  background-color: rgba(0, 0, 0, .875);
  border-radius: 5px;
  margin-top: -185px;
  margin-left: auto;
  margin-right: 17px;
  padding-top: 10px;
  padding-left: 10px;
  transition: all .3s;
