}

@keyframes shadow-animation {
  0% {
    box-shadow: 0 0 rgba(255, 0, 27, .42);
  }

  50% {
    box-shadow: 0 0 0 8px rgba(255, 0, 27, .42);
  }

  100% {
    box-shadow: 0 0 0 4px rgba(255, 0, 27, .42);
  }
}

@keyframes scale-up-right {
  0% {
    transform-origin: 100% 100%;
    transform: scale(0);
  }

  100% {
    transform-origin: 100% 100%;
    opacity: 1;
    transform: scale(1);
  }
}

